@use "base/variables" as v;
@use "base/mixins" as m;
.nuestros-testimoniales {
  padding: 10rem 2rem;
  @include m.tablet {
    padding: 10rem;
  }
  .testimonial {
    background-color: v.$cafeClaro;
    padding: 2rem 4rem;
    max-width: 50rem;
    margin: 0 auto;
    border-radius: 1rem;
    .autor { 
        text-align: right;
        color: v.$primario;
        font-size: 2.4rem;
        font-family: v.$fuenteCursiva;
    }
  }
}
