@use "variables" as v;
h1,
h2,
h3 {
  font-family: v.$fuentePrincipal;
  margin: 0 0 v.$separacion 0;
  text-align: center;
  line-height: 1.5;
  text-transform: uppercase;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
  color: v.$primario;
  span {
    color: v.$secundario;
    font-size: 1.8rem;
    display: block;
  }

  &::after {
    content: "";
    display: block;
    width: 10rem;
    height: 10rem;

    background-image: url(../img/cafe.svg);
    margin: 0 auto;
    background-size: 10rem;
  }
}

.heading-blanco {
  @extend h2;
  color: v.$blanco;
  span {
    color: v.$blanco;
  }
  &::after {
    display: none;
  }
}

h3 {
  font-size: 3.2rem;
}
